import React,{ useState } from 'react'
import styled from "styled-components" 
//import ReactLogo from './logo.svg';
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import { selectCars} from '../features/carslice/carSlice'
import{useSelector}from 'react-redux';


function Header() {
    const [burgerState,setBurgerStatus]=useState(false); 
    const cars=useSelector(selectCars);
    return (
        <Container>
            <Logo>
                <img src={"/images/ENXIAN_LOGO.png"} alt="Enxian_LOGO" />
            </Logo>
            

            <RightMenu>
                <a href="#">Home</a>
                <a href="#">About</a>
                <a href="#">Contact</a>
                <a href="#">Login</a>
                <MenuWrapper>
                    <CustomMenu style={{ fontSize: 40 }}onClick={()=>setBurgerStatus(true)}/>
                </MenuWrapper>

                
            </RightMenu>

            <Hamburger show={burgerState}>
                <CloseWrapper>
                    <CustomClose onClick={()=>setBurgerStatus(false)}/>
                </CloseWrapper>
                
                <li><a href="#">Home</a></li>
                <li><a href="#">About</a></li>
                <li><a href="#">Contact</a></li>
                <li><a href="#">Login</a></li>

            </Hamburger>
             

            
               
        </Container>
       
    )
}

export default Header

const Container = styled.div`
    min-height:60px;
    position:fixed;
    display:flex;
    align-items:center;
    justify-content:space-between;
    z-index:1;
    top:0;
    left:0;
    right:0;
`
const Logo= styled.div`
    padding-left:5px;
    padding-top:10px;

    @media(max-width:768px){
        img{
            width:350px;
            height:40px;
        }
    }
`
const RightMenu=styled.div`
    display:flex;
    align-items:center;
    padding-right:15px;

    a{
        font-size:20px;
        font-weight:600;
        padding:0 30px;
        flex-wrap:no-wrap;

    }

    @media(max-width:768px){
        a{
            display:none;
        }

        
        
    }


`

const CustomMenu=styled(MenuIcon)`
    font-size:40px;
    font-weight:600;
    cursor:pointer;

`

const Hamburger=styled.div`
    position:fixed;
    top:0;
    bottom:0;
    right:0;
    background:white;
    width:300px;
    z-index:100;
    list-style:none;
    padding:20px;
    display:flex;
    flex-direction:column;
    transform:${props =>props.show ? 'translateX:(0)':'translateX(100%)'};
    transition:transform 0.2s; //ease-in
    li{
        padding:15px 0;
        border-bottom:1px solid black;
        
        a{
            font-weight:600;
        }


    }





`

const CustomClose=styled(CloseIcon)`
cursor:pointer;
`

const CloseWrapper=styled.div`
    display:flex;
    justify-content:flex-end;
    
`
const MenuWrapper=styled.div`
    display:none;
    @media(max-width:768px){
        display:flex;
    }

`

