import React from 'react'
import Fade from 'react-reveal/Fade';
import styled from "styled-components"

function Introduction({BtnText}) {
    return (
        <Wrap bgImage={"Pigments.jpg"}>
            <Looping>
                <loop></loop>
            </Looping>
            
            <Buttons>
                <Fade bottom>
                    <CardButton>{BtnText}</CardButton>
                </Fade>
                
                

                <Arrow src="./images/down-arrow.svg"></Arrow>

            </Buttons>

            
            
        </Wrap>
    )
}

export default Introduction


const Wrap= styled.div`
    width:100vw;
    height:100vh;
    background-color:black;
    background-size:cover;
    background-position:center;
    background-repeat:no-repeat;
    display:flex;
    flex-direction:column;
    justify-content:space-between;    //vertical
    align-items:center;               //horizontal
    //background-image: ${props =>`url("/images/${props.bgImage}")`}

    
`
const Looping=styled.div`
    justify-content:center;
    text-align:center;
    padding-top:25vh;
    width:700px;
    height:100px;
    @media(max-width:768px){
        width:450px;
        height:600px;
    }

    
    

    loop:before{
        content:'God may work in light, but we mortals work in pigment';
        color:white;
        font-size: 2.5em;
        font-weight: 100;
        animation:changing1 infinite 15s;
    

    }
@keyframes changing1{
    0%{
        content:'God may work in light, but we mortals work in pigment';
        color:red;
        opacity: 0;
        transform: rotateX(10deg);
        animation-duration: 2.5s;
    }
    20%{
        content:'God may work in light, but we mortals work in pigment';
        color:orange;
        opacity: 1;
        transform: rotateX(-10deg);
        animation-duration: 2.5s;
    }
    40%{
        content:'Seperation penetrates the disappearing person like a pigment and steep him in gentle radiance';
        color:yellow;
        opacity: 0;
        transform: rotateX(10deg);
        animation-duration: 2.5s;
        
    }
    60%{
        content:'Seperation penetrates the disappearing person like a pigment and steep him in gentle radiance';
        color:green;
        opacity: 1;
        transform: rotateX(-10deg);
        animation-duration: 2.5s;
       
    }
    80%{
        content:'Colour is as variable and evanescent in the form of pigment as in visible nature.';
        color:blue;
        opacity: 0;
        transform: rotateX(10deg);
        animation-duration: 2.5s;

    }
    100%{
        content:'Colour is as variable and evanescent in the form of pigment as in visible nature.';
        color:violet;
        opacity: 1;
        transform: rotateX(-10deg);
        animation-duration: 2.5s;

    }
    
    
}
`
const Buttons = styled.div`
    flex-direction:column;

    
`
const CardButton=styled.div`
    font-size:1em;
    color:white;
    background:red;
    display:flex;
    padding:10px 30px;
    margin-bottom:40px;
    text-decoration: none;
    letter-spacing: 3px;
    transition: 0.5s;
    
        
    
    :hover{
        letter-spacing: 6px;
        font-size: larger;
    }

`
const Arrow=styled.img`
    margin-top:20px;
    height:40px;
    overflow-x:hidden;
    animation: animateDown infinite 1.5s


`



